import React, { useState, useEffect, useRef } from "react";
import { Stage, Layer, Star, Text, Image, Rect } from 'react-konva';
import useImage from 'use-image';
import URLImage from "./components/URLImage";
import axios from "axios";

function MapPage() {

  const stageRef = useRef(null);
  
  const [isTreeVis, setTreeVisible] = React.useState(true);
  const [isMineVis, setMineVisible] = React.useState(true);

  const [plots, setPlots] = React.useState(null);
  const [myPlots, setMyPlots] = React.useState(null);
  const [isLoaded, setLoaded] = React.useState(false);

  const [newScale, setNewScale] = React.useState(1);
  const [scaleBy, setScaleBy] = React.useState(0.1);
  
  const [offsetX, setOffsetX] = React.useState(0);
  const [offsetY, setOffsetY] = React.useState(0);

  const [mapWidth, setMapWidth] = React.useState(window.innerWidth);
  const [mapHeight, setMapHeight] = React.useState(window.innerHeight);
  const [stageWidth, setStageWidth] = React.useState(10000);
  const [stageHeight, setStageHeight] = React.useState(6750);
  const [blockSnapSize, setBlockSnapSize] = React.useState(10);
  
  const [clickedSearch, setClickedSearch] = React.useState(0);
  const [searchX, setSearchX] = React.useState(0);
  const [searchY, setSearchY] = React.useState(0);
  const [selNftId, setNftId] = React.useState(0);

  const [selBlockX, setSelBlockX] = React.useState(0);
  const [selBlockY, setSelBlockY] = React.useState(0);
  
  const [selBlock, setSelBlock] = React.useState(null);
  
//  height, setDragging
  const [isDraggable, setDragging] = React.useState(true)

  if(isLoaded === false){
    console.log("good")
    setLoaded(true)
    axios
      .get('/nfts/0xee4F684Cc0252d698bf65564B486Ad77da3e15Df')
      // .get('/nfts/0xee4F684Cc0252d698bf65564B486Ad77da3e15Df')
      .then(response => {
        setMyPlots(response.data)
      }
    )
    axios
      .get('/getall')
      // .get('http://localhost:5000/getall')
      .then(response => {
        setPlots(response.data)
      }
    )
  }
  const imagePortal = new window.Image();
  imagePortal.src = "/assets/background.jpg";
  // imagePortal.src = "http://localhost:5000/assets/background.jpg";
  const imageTree = new window.Image();
  imageTree.src = "/assets/trees.png";
  // imageTree.src = "http://localhost:5000/assets/trees.png";
  const imageNFTs = new window.Image();
  imageNFTs.src = "/images/test.png";
  // imageNFTs.src = "http://localhost:5000/images/test.png";

  const zoomIn = () => {
    setNewScale(newScale+scaleBy)
  }
  
  const zoomOut = () => {
    setNewScale(newScale-scaleBy)
  }
  const closeSelBlock = () => {
    setSelBlock(null)
  }
  const searchByPlot = () => {
    let searchStatus = clickedSearch;
    
    let lsearchX
    let lsearchY
    setClickedSearch(0)
    if(searchStatus === 1){
      lsearchX = searchX
      lsearchY = searchY
    } else {
      console.log(typeof selNftId)
      let nftId = parseInt(selNftId)
      Object.keys(plots).map((key, index) => {
        if(plots[key]['landID'] === nftId){
          lsearchX = plots[key]['posX']
          lsearchY = plots[key]['posY']
        }  
      })
    }
    setOffsetX(-1*lsearchX*10*newScale+window.innerWidth/2)
    setOffsetY(-1*lsearchY*10*newScale+window.innerHeight/2)

    setSelBlockX(lsearchX*10)
    setSelBlockY(lsearchY*10)
    console.log(searchX, offsetX)
    console.log(searchY, offsetY)
  }
  const mouseWheelZoom = (e, scaleBy, stageWidth, stageHeight, mapWidth, mapHeight, stageRef) => {
    
    e.evt.preventDefault();
    let stage = e.target.getStage();

    // setMapOpen(false);

    let oldScale = newScale;
    let pointer = stage.getPointerPosition();

    let mousePointTo = {
        x: (pointer.x - stage.x()) / oldScale,
        y: (pointer.y - stage.y()) / oldScale,
    };

    // how to scale? Zoom in? Or zoom out?
    let direction = e.evt.deltaY > 0 ? 1 : -1;

    // when we zoom on trackpad, e.evt.ctrlKey is true
    // in that case lets revert direction
    if (e.evt.ctrlKey) {
        direction = -direction;
    }

    let newScale2 = direction > 0 ? oldScale + scaleBy : oldScale - scaleBy;
    // setNewScale(newScale);
    
    let posX = pointer.x - mousePointTo.x * newScale2;
    let posY = pointer.y - mousePointTo.y * newScale2;
    
    //  .
    console.log(stageRef.x(), mapHeight, (stageRef.x()/oldScale - mapHeight * newScale2) / newScale2)
    if (stageHeight * newScale2 > mapHeight && stageWidth * newScale2 > mapWidth) {
      if (posY > 0) {
        posY = 0;
      }
      if (posX > 0) {
        posX = 0;
      }
      
      if (posY < mapHeight - stageHeight * newScale2) {
          posY = mapHeight - stageHeight * newScale2;
      }

      if (posX < mapWidth - stageWidth * newScale2) {
        posX = mapWidth - stageWidth * newScale2;
      }
      
      let position = {
        x: posX,
        y: posY,
      };
      stage.position(position);
      stage.scale({ x: newScale2, y: newScale2 });
      // only in desktop
      // getParcellRange(true, stage, mapWidth, mapHeight, blockSnapSize, setVillImageList, parcelList);
      console.log("new", newScale2)
      setNewScale(newScale2);
    }
    
  }
  const dragBound = (pos, mapWidth, mapHeight, stageWidth, stageHeight, originScale, setDragging) => {
    setDragging(true)
    if (pos.y > 0) {
        pos.y = 0;
    }
    if (pos.x > 0) {
        pos.x = 0;
    }

    // scale  .
    if (pos.y < mapHeight - stageHeight * originScale) {
        pos.y = mapHeight - stageHeight * originScale;
    }

    if (pos.x < mapWidth - stageWidth * originScale) {
        pos.x = mapWidth - stageWidth * originScale;
    }

    // if (-1 * (stageHeight * originScale * -1) === height) {
    //     pos.y = 0;
    // }
    return pos;
  }
  const searchDiv = clickedSearch === 1?(<>
    <div className="row">
      <div className="inputTitle">X-coordinate</div>
      <input value={searchY} onChange={(e) => setSearchY(e.target.value)} type="text" placeholder="Enter your x-coordinate" />
    </div>
    <div className="row">
      <div className="inputTitle">Y-coordinate</div>
      <input value={searchX} onChange={(e) => setSearchX(e.target.value)} type="text" placeholder="Enter your y-coordinate" />
    </div></>):(
    <div className="row">
      <div className="inputTitle">NFT ID</div>
      <input value={selNftId} onChange={(e) => setNftId(e.target.value)} type="text" placeholder="Enter your nft id" />
    </div>);
  return (
    <>
      <div className="btnWrap">
        <button className="btnZoom" onClick={zoomIn}>+</button>
        <button className="btnZoom" onClick={zoomOut}>-</button>
        <button onClick={()=>setClickedSearch(1)} className="btnSearch">
          <div className="iconSearch"></div>
        </button>
        <button onClick={()=>setClickedSearch(2)} className="btnSearch">
          <div className="iconSearch"></div>
        </button>
      </div>
      
      <Stage ref={stageRef}
        width={window.innerWidth} 
        height={window.innerHeight}
        scaleX={newScale}
        scaleY={newScale}
        x={offsetX}
        y={offsetY}
        draggable={isDraggable}
        // onMouseEnter={(e) => {
        //   if (isBrowser) {
        //       e.evt.preventDefault();
        //       // stageMouseMove(e);
        //       stageMouseMove(e, groupRef.current, parcelList, shadowRectRef.current, blockSnapSize)
        //   }
        // }}
        onMouseUp={(e) => {
            e.evt.preventDefault();
        }}
        onMouseOver={(e) => {
            e.evt.preventDefault();
        }}
        
        onMouseMove={(e) => {
          e.evt.preventDefault();
          let stage = e.target.getStage();
          
          let pointer = stage.getRelativePointerPosition();
          setSelBlockX(parseInt(pointer.x/10)*10)
          setSelBlockY(parseInt(pointer.y/10)*10)
          console.log(selBlockX, selBlockY)
        }}
        onWheel={(e) => {
            console.log(newScale)
            mouseWheelZoom(e, scaleBy, stageWidth, stageHeight, mapWidth, mapHeight, stageRef.current);
        }}

        dragBoundFunc={(position) => {
          return dragBound(position, mapWidth, mapHeight, stageWidth, stageHeight, newScale, setDragging)
        }}
        // onTouchStart={(e) => {
        //     e.evt.preventDefault();
        //     touchStartCheck = true;
        //     if (e.evt.touches.length === 1) {
        //         let stage = e.target.getStage();
        //         if (isMobile) {
        //             mobileGrid(stage, stageWidth, stageHeight, setGridList, true)
        //         }
        //         setTouchZoom(false);
        //     }
        // }}
        // onTouchMove={touchMove}
        // onTouchEnd={touchEnd}
        onClick={(e) => {
          let stage = e.target.getStage();
          // let position = stage.getPointerPosition()
          let relativePosition = stage.getRelativePointerPosition()
          let parcelPosition = {
            parcelX: parseInt(relativePosition.x / blockSnapSize),
            parcelY: parseInt(relativePosition.y / blockSnapSize),
          };
          if (typeof (plots[parcelPosition.parcelX+"_"+parcelPosition.parcelY]) !== 'undefined') {
            setSelBlock(plots[parcelPosition.parcelX+"_"+parcelPosition.parcelY])
            console.log("clicked", parcelPosition)
          } else {
            setSelBlock(null)
          }
        }}
        >
        <Layer>
          <Image image={imagePortal} x={0} y={0} />
          <Image image={imageTree} x={0} y={0} visible={isTreeVis}/>
          <Image image={imageNFTs} x={0} y={0} visible={isTreeVis}/>
          {myPlots !== null? Object.keys(myPlots).map((key, index) => (
            <Rect
              key={index}
              x={myPlots[key]['posX']*10}
              y={myPlots[key]['posY']*10}
              width={10}
              height={10}
              fill="red"
              stroke='black'
              strokeWidth={1}
            />
          )):""}
          <Rect
            x={selBlockX}
            y={selBlockY}
            width={10}
            height={10}
            fill="yellow"
            stroke='black'
            strokeWidth={1}
          />
        
        </Layer>
      </Stage>
      {selBlock !== null?(
      <div id="terrainModal" className="mi-modal">

        <div className="mi-modal-content">
          <div className="mi-modal-header">
            <span className="mi-close" onClick={closeSelBlock}>&times;</span>
            <h2>Ocean #{selBlock['landID']}</h2>
          </div>
          <div className="mi-modal-body">
            <img id = "terrain" src="/assets/terrain.gif" />
            {/* <img id = "terrain" src="http://localhost:5000/assets/terrain.gif" /> */}
            <div className="terrain-content">
              Owned By {selBlock['owner'].substring(0,4)+"..."+selBlock['owner'].substr(-4) }
              <br />
              <br />
              <div>
                Plot: {selBlock['posX']} , {selBlock['posY']}
              </div>
              <ul>
                <li>
                  White
                  <div className="status-comment">
                    Not minted and available for your to buy.
                  </div>
                </li>
                <li>
                  Grey
                  <div className="status-comment">
                    Minted, but not available in our marketplace.
                  </div>
                </li>
                <li>
                  Blue
                  <div className="status-comment">
                    Minted and available in our marketplace.
                  </div>  
                </li>
                <li>
                  Green
                  <div className="status-comment">
                    Your land! Add 3D assets now. Visit our marketplace and buy some.
                  </div>  
                </li>
              </ul>
              <br />
              <div className = "buy-button">
                <button>Buy</button>
              </div>
            </div>
          </div>
        </div>
      </div>):""}
      {clickedSearch !== 0?(<div>
        <div className="popupWrap">
          <div className="popupArea popup2">
            <button className="btnClose" onClick={()=>setClickedSearch(0)}></button>
            <div className="popupContents">
              <div className="popupTitle">Search</div>
              <div className="popupText2">
                {searchDiv}
                <div className="row">
                  <button  className="btnLOGIN" onClick={searchByPlot}>Search</button>
                </div>
              </div>
            </div>
          </div>
          <div className="popupBg"></div>
        </div>
      </div>):""}
    </>
    
  );
}

export default MapPage;
